.getintouch {
   
    position: absolute;
    left: 30px;
    padding-top: 8rem;
    font-size:15rem; 
    font-family:Arial, Helvetica, sans-serif;
    line-height: 13rem;
  -webkit-text-fill-color: rgb(0, 0, 0); /* Will override color (regardless of order) */
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: rgb(123, 117, 117);
}

.contact-info {
    color: rgb(227, 225, 225);
    position: absolute;
    right: 10vw; 
    padding-top: 20vh;
    font-size: 1vw; 
    font-weight: bold;
    font-family: 'Poppins', sans-serif;
}
.contact {
    display: inline;
    
}
.email, .tel, .send-us {
   margin-top: 40px;
   margin-bottom: 1px;
   color: rgb(123, 117, 117);
   font-family: 'Poppins', sans-serif;
   font-weight: bold;
   font-size: 1.1vw;
   
}

.faq {
    height: 100vh;
    width: 100vw;
    opacity: 40%;
    position: absolute;
    top: 0px;
    left: 0px;
    -webkit-mask-image:-webkit-gradient(linear, left top, left bottom, from(rgba(0,0,0,1)), to(rgba(0,0,0,0)));
      mask-image: linear-gradient(to bottom, rgba(0,0,0,1), rgba(0,0,0,0));
}

.faq-title{
    color: white;
    font-size: 3rem;
    display: flex;
    justify-content: center;
    text-align: center;
    padding-top: 10%;
    padding-bottom: 5%;
    padding-left: 5%;
    position: relative;
    z-index: 999;
    
}

.faq-container{
    width: 100vw;
    background: black;
    position: relative;
    padding-bottom: 5%;
    font-family: 'Poppins', sans-serif;
}


.faq-questions{
    text-align: left; 
    padding-right: 20%;
    padding-left: 20%;
    font-family: 'Poppins', sans-serif;
    
}

.question {

    position: relative;
    z-index: 999;

    margin-top: 1rem;
    padding-top: 20px;

    border-top: solid white 0.5px;
    font-size: 1.4vw;
    font-weight: bold;
    color: rgba(249, 253, 255, 0.868);
    
    
}

.response {
   font-size: 1.1vw;
   font-weight: lighter;
   color: white;
}

li{
    font-weight: normal;
    color: white;
    font-size: 1.2vw;
   text-decoration: none;
    
} 